.header {
    grid-area: h;
    background-color: #282c34;
    display: flex;
    align-items: center;
}

.header__container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.logo {
    height: 100px;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.login {
    color: transparent;
    width: 100px;
    height: 100px;
    background-color: white;
    border-radius: 50%;

}

.avatar {
    width: 50px;
    height: 50px;
    border: 3px solid white;
    border-radius: 50%;

}

.avatar__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.avatar__container button {
    width: 100%;
    background-color: aqua;
    margin-top: 1em;
    border-radius: 5px;
    cursor: pointer;
}